<template>
  <div id="container">
    <div id="settings">
      <div class="user-pic">
        <img :src="photo" alt="User Pic" @click.prevent="this.update()">
      </div>
<!--      <div class="user-name">{{ this.first_name }} {{ this.last_name }}</div>-->
      <form class="settings-form" action="">
        <div class="form-control">
          <label class="form-label" for="name">Имя</label>
          <input class="form-input" id="name" name="name" v-model="first_name" type="text">
        </div>
        <div class="form-control">
          <label class="form-label" for="last_name">Фамилия</label>
          <input class="form-input" id="last_name" name="last_name" v-model="last_name" type="text">
        </div>
        <div class="form-control">
          <label class="form-label" for="email">Эл. почта</label>
          <input class="form-input" id="email" name="email" v-model="email" type="text">
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Settings",
  data: () => ({
    user_id: 0,
    photo: '',
    first_name: '',
    last_name: '',
    email: '',
  }),
  mounted() {
    const telegram = window.Telegram.WebApp;
    const telegramData = telegram.initDataUnsafe;
    const mainButton = telegram.MainButton;
    const backButton = telegram.BackButton;

    this.user_id = telegramData.user.id

    axios
        .get('https://back.sombra.uz/api/user/' + this.user_id)
        .then(response => {
          if (response.data.data) {
            this.photo = response.data.data.photo
            this.first_name = response.data.data.first_name
            this.last_name = response.data.data.last_name
            this.email = response.data.data.email
          }
        })
        .catch(error => console.log(error));

    backButton.show()
    backButton.onClick(() => {
      this.$router.push('/')
    });

    mainButton.show()
    mainButton.setParams({
      'text': "Сохранить",
      'color': "#f2c058",
      'text_color': "#FFF"
    })
    mainButton.onClick(() => {
      mainButton.showProgress()
      axios
          .post('https://back.sombra.uz/api/user/' + this.user_id, {
            first_name: this.first_name,
            last_name: this.last_name,
            email: this.email
          })
          .then(response => {
            this.$router.push('/')
          })
          .catch(error => {
            telegram.showPopup(error)
          })
    })
  }
}
</script>

<style scoped>
#container {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (min-height: 734px) {
  #container {
    width: 100%;
    height: auto;
    padding: 0 35px;
    margin-bottom: 0;
    position: relative;
    top: 90px;
  }
}

@media screen and (min-height: 700px) {
  #container {
    width: 100%;
    height: auto;
    padding: 0 35px;
    margin-bottom: 0;
    position: relative;
    top: 90px;
  }
}

@media screen and (min-height: 498px) {
  #container {
    width: 100%;
    height: auto;
    padding: 0 35px;
    margin-bottom: 200px;
    position: relative;
    top: 80px;
  }
}
</style>